import { useCallback } from 'react';
import { isServerSide } from '../helpers.js';

function useHistoryBackCallback(callback) {
  return useCallback(function () {
    if (!isServerSide() && window.history.state === 'overlay-open') {
      window.history.back();
    }

    if (callback) {
      callback();
    }
  }, [callback]);
}

export default useHistoryBackCallback;
