import { useRef, useCallback, useEffect } from 'react';

/**
 * check to see if a component is mounted or not
 *
 * @example
 *   useEffect(() => {
 *      fetchStuff().then(data => isMounted() && setData(data))
 *   }, [])
 *
 */

function useMountState() {
  var ref = useRef(false);
  var get = useCallback(function () {
    return ref.current;
  }, []);
  useEffect(function () {
    ref.current = true;
    return function () {
      ref.current = false;
    };
  }, []);
  return get;
}

export default useMountState;
